 // 
var videoAd = null;
let rewarded = {
	//idid
	load(id,e) {
		if (uni.createRewardedVideoAd) {
			videoAd = uni.createRewardedVideoAd({
				adUnitId: id
			})
			videoAd.onLoad(() => {
				 console.log(' ')
			})
			videoAd.onError((err) => {
				console.log('onError event emit')
			})
			videoAd.onClose((res) => {
				// 
				if (res && res.isEnded) {
					// 
					e()
					console.log('')
                    videoAd.offClose()
				} else {
					uni.showToast({
						icon:'none',
						title:''
					})
					// 
					console.log('')
				}
			})
		}
	},
	show() {
		if (videoAd) {
			videoAd.show().catch(() => {
				// 
				videoAd.load()
					.then(() => videoAd.show())
					.catch(err => {
						console.log(' ')
					})
			})
		}
	}
}
 
module.exports = {
	rewarded
};