import urlConfig from '../common/config.js'
import store from '../store'
export default {
	/*
	 * 
	 * @param object opt
	 * @param callable successCallback  data 
	 * @param callable errorCallback  
	 */
	uploadImageOne: function(opt, successCallback, errorCallback) {
		let that = this;
		if (typeof opt === 'string') {
			let url = opt;
			opt = {};
			opt.url = url;
		}
		let count = opt.count || 1,
			sizeType = opt.sizeType || ['compressed'],
			sourceType = opt.sourceType || ['album', 'camera'],
			is_load = opt.is_load || true,
			uploadUrl = opt.url || '',
			inputName = opt.name || 'field';
		uni.chooseImage({
			count: count, //  
			sizeType: sizeType, //   
			sourceType: sourceType, //   
			success: function(res) {
				//...  
				uni.showLoading({
					title: '',
				});
				uni.uploadFile({
					url: urlConfig + '/api/' + uploadUrl + '/' + inputName,
					filePath: res.tempFilePaths[0],
					name: inputName,
					formData: {
						'filename': inputName
					},
					header: {

						"Content-Type": "multipart/form-data",

						['x-token']: store.state.userInfo.token
					},
					success: function(res) {
						uni.hideLoading();
						if (res.statusCode == 403) {
							uni.showToast({
								title: res.data,
								icon: 'none'
							})
						} else {
							let data = res.data ? JSON.parse(res.data) : {};
							if (data.status == 200) {
								successCallback && successCallback(data)
							} else {
								errorCallback && errorCallback(data);
								uni.showToast({
									title: data.msg,
									icon: 'none'
								})
							}
						}
					},
					fail: function(res) {
						uni.hideLoading();
						uni.showToast({
							title: '',
							icon: 'none'
						})
					}
				})
				// pathToBase64(res.tempFilePaths[0])
				// 	.then(imgBase64 => {
				// 		console.log(imgBase64);

				// 	})
				// 	.catch(error => {
				// 		console.error(error)
				// 	})
			}
		})
	},
	/**
	 * opt  object | string
	 * to_url object | string
	 * :
	 * this.Tips('/pages/test/test'); 
	 * this.Tips({title:''},'/pages/test/test'); 
	 * this.Tips({title:''},{tab:1,url:'/pages/index/index'}); table
	 * tab=1  table
	 * tab=2  table
	 * tab=3 
	 * tab=4 table
	 * tab=5 table
	 */
	Tips: function(opt, to_url) {
		if (typeof opt == 'string') {
			to_url = opt;
			opt = {};
		}
		let title = opt.title || '',
			icon = opt.icon || 'none',
			endtime = opt.endtime || 2000,
			success = opt.success;
		if (title) uni.showToast({
			title: title,
			icon: icon,
			duration: endtime,
			success
		})
		if (to_url != undefined) {
			if (typeof to_url == 'object') {
				let tab = to_url.tab || 1,
					url = to_url.url || '';
				switch (tab) {
					case 1:
						// table
						setTimeout(function() {
							uni.switchTab({
								url: url
							})
						}, endtime);
						break;
					case 2:
						//table
						setTimeout(function() {
							uni.navigateTo({
								url: url,
							})
						}, endtime);
						break;
					case 3:
						//
						setTimeout(function() {

							uni.navigateBack({
								delta: parseInt(url),
							})




						}, endtime);
						break;
					case 4:
						//table
						setTimeout(function() {
							uni.reLaunch({
								url: url,
							})
						}, endtime);
						break;
					case 5:
						//table
						setTimeout(function() {
							uni.redirectTo({
								url: url,
							})
						}, endtime);
						break;
				}
	
			} else if (typeof to_url == 'function') {
				setTimeout(function() {
					to_url && to_url();
				}, endtime);
			} else {
				//
				setTimeout(function() {
					uni.navigateTo({
						url: to_url,
					})
				}, title ? endtime : 0);
			}
		}
	},
	/**
	 * 
	 * @obj 
	 * @state 0  1
	 * 
	 *
	 * **/
	stringIntercept: function(obj, state, type) {
		var index = obj.lastIndexOf(type);
		if (state == 0) {
			obj = obj.substring(0, index);
		} else {
			obj = obj.substring(index + 1, obj.length);
		}
		return obj;
	},
	/*
	 * 
	 */
	SplitArray(list, sp) {
		if (typeof list != 'object') return [];
		if (sp === undefined) sp = [];
		for (var i = 0; i < list.length; i++) {
			sp.push(list[i]);
		}
		return sp;
	},
	trim(str) {
		return String.prototype.trim.call(str);
	},
	$h: {
		//
		//javascript
		//$h.Div(arg1,arg2)
		//arg1arg2
		Div: function(arg1, arg2) {
			arg1 = parseFloat(arg1);
			arg2 = parseFloat(arg2);
			var t1 = 0,
				t2 = 0,
				r1, r2;
			try {
				t1 = arg1.toString().split(".")[1].length;
			} catch (e) {}
			try {
				t2 = arg2.toString().split(".")[1].length;
			} catch (e) {}
			r1 = Number(arg1.toString().replace(".", ""));
			r2 = Number(arg2.toString().replace(".", ""));
			return this.Mul(r1 / r2, Math.pow(10, t2 - t1));
		},
		//
		//javascript
		//$h.Add(arg1,arg2)
		//arg1arg2
		Add: function(arg1, arg2) {
			arg2 = parseFloat(arg2);
			var r1, r2, m;
			try {
				r1 = arg1.toString().split(".")[1].length
			} catch (e) {
				r1 = 0
			}
			try {
				r2 = arg2.toString().split(".")[1].length
			} catch (e) {
				r2 = 0
			}
			m = Math.pow(100, Math.max(r1, r2));
			return (this.Mul(arg1, m) + this.Mul(arg2, m)) / m;
		},
		//
		//javascript
		//$h.Sub(arg1,arg2)
		//arg1arg2
		Sub: function(arg1, arg2) {
			arg1 = parseFloat(arg1);
			arg2 = parseFloat(arg2);
			var r1, r2, m, n;
			try {
				r1 = arg1.toString().split(".")[1].length
			} catch (e) {
				r1 = 0
			}
			try {
				r2 = arg2.toString().split(".")[1].length
			} catch (e) {
				r2 = 0
			}
			m = Math.pow(10, Math.max(r1, r2));
			//
			n = (r1 >= r2) ? r1 : r2;
			return ((this.Mul(arg1, m) - this.Mul(arg2, m)) / m).toFixed(n);
		},
		//
		//javascript
		//$h.Mul(arg1,arg2)
		//arg1arg2
		Mul: function(arg1, arg2) {
			arg1 = parseFloat(arg1);
			arg2 = parseFloat(arg2);
			var m = 0,
				s1 = arg1.toString(),
				s2 = arg2.toString();
			try {
				m += s1.split(".")[1].length
			} catch (e) {}
			try {
				m += s2.split(".")[1].length
			} catch (e) {}
			return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m);
		},
	},
}
