import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
	state() {
		return {
			token: '',
			baseInfo: {},
			userInfo: {},
			hasLogin: false
		}
	},
	getters: {},
	mutations: {
		/**
		 * 
		 */
		writeBaseInfo(state, info) {
			state.baseInfo = info
		},
		/**
		 * 
		 * @param {Object} info 
		 */
		writeInfo(state, info) {
			state.userInfo = info
		},
		/**
		 * 
		 * @param {String} token
		 */
		login(state, token) {
			state.token = token
			state.hasLogin = true
			uni.setStorage({
				key: 'token',
				data: token
			})
		},
		// 
		logOut(state) {
			state.token = ''
			state.userInfo = {}
			state.hasLogin = false
			uni.removeStorage({
				key: 'token'
			})
		}
	},
	actions: {}
})

export default store
